@import "https://unpkg.com/open-props";
@import "https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700;9..144,800&display=swap";
@import "https://fonts.googleapis.com/css2?family=Barlow:wght@600&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #0a0a0a;
  background: var(--gray-2);
  font-family: var(--font-sans);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
  position: relative;
}

.grid {
  grid-template: "sunny sunny sunny sunny" 1fr
                 "s1 s1 s2 s2" .5fr
                 "s3 s3 s4 s4" .5fr
                 "s5 s5 s6 s6" .5fr
                 "client client client client" 1fr
                 "p1 p2 p3 p4" .5fr
                 "social social social social" .5fr
                 / 1fr 1fr 1fr 1fr;
  width: min(100% - .1rem, 100em);
  height: 100%;
  margin-inline: auto;
  display: grid;
}

.fa-bars {
  visibility: hidden;
  cursor: none;
  align-items: center;
  font-size: 2em;
  position: absolute;
  right: 35px;
}

.fa-bars:hover {
  color: #ffffff38;
  cursor: pointer;
}

header {
  background-image: url("https://i.ibb.co/S59fFKk/image-header.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 800px;
  font-family: Barlow, sans-serif;
}

header h1 {
  letter-spacing: .24em;
  color: #fff;
  text-align: center;
  justify-content: center;
  margin-top: 5rem;
  font-family: Fraunces;
  font-size: clamp(2.25rem, 5vw, 4em);
  display: flex;
}

header .sunnyside {
  width: 10em;
}

header .downArrow {
  margin-inline: auto;
  width: 1.85em;
  margin-top: 6.55em;
  scale: 1.2;
}

header nav {
  color: #fff;
  justify-content: space-between;
  padding: 2rem;
  display: flex;
}

header nav ul {
  z-index: 10;
  align-items: center;
  gap: 4em;
  font-weight: 600;
  text-decoration: none;
  list-style: none;
  display: inline-flex;
}

header nav ul li:hover {
  cursor: pointer;
  transition: all .2s ease-in;
  transform: translateY(-1px)scale3d(1.03, 1.03, 1);
}

header nav ul li:nth-child(4) {
  color: #000;
  background: #fff;
  border-radius: 25px;
  padding: .85rem 1.5rem;
  font-family: Fraunces, sans-serif;
}

header nav ul li:nth-child(4):hover {
  color: #fff;
  cursor: pointer;
  background: #ffffff3b;
  transition: background .25s;
}

img {
  object-fit: cover;
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

.text {
  background-color: #fff;
  place-content: center;
  padding-block: min(10vh, 3em);
  padding-inline: min(10vw, 12em);
  display: grid;
}

.text h2 {
  font-family: Fraunces;
  font-size: 2.35em;
  font-weight: 900;
}

.text p {
  color: #818498;
  margin-top: 2em;
  line-height: 1.5rem;
}

.text .learn {
  margin-top: 2.5em;
  font-family: Fraunces;
  font-weight: 900;
  position: relative;
}

.text .y1 {
  background: #ffcc0039;
  border-radius: 5px;
  width: 128px;
  height: 10px;
  translate: -8px 62px;
}

.text .y2 {
  background: #ff000028;
  border-radius: 5px;
  width: 128px;
  height: 10px;
  translate: -8px 62px;
}

.text- {
  place-content: center;
  height: 40em;
  padding-inline: min(10vw, 12em);
  display: grid;
}

.text- h2 {
  color: #25564b;
  margin-top: 12em;
  margin-inline: auto;
  font-family: Fraunces;
  font-size: 1.9em;
  font-weight: 900;
}

.text- p {
  color: #25564b;
  text-align: center;
  margin-top: 2em;
  padding: 0 2.4em;
  line-height: 1.5rem;
}

.g1 {
  background-image: url("https://i.ibb.co/qkw79Tw/image-graphic-design.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.g2 {
  background-image: url("https://i.ibb.co/0VVkfMc/image-photography.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.client {
  background: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-block: min(10vh, 9em);
  display: flex;
}

.client h2 {
  letter-spacing: clamp(.15rem, 1.8vw, 1.05rem);
  color: #a7abae;
  text-align: center;
  font-family: Fraunces;
  font-size: clamp(1rem, 1.8vw, 1.25rem);
  font-weight: 900;
}

.group_profile {
  display: flex;
}

.profile {
  padding-inline: min(10vw, 7em);
}

.profile p {
  text-align: center;
  color: #23303e;
  margin-top: 3.8em;
  line-height: 1.5rem;
}

.profile h3 {
  text-align: center;
  margin-top: 3.8em;
  font-family: Fraunces;
}

.profile h6 {
  text-align: center;
  color: #a7abae;
  margin-top: .5rem;
  font-size: .9px;
}

.profile img {
  margin-inline: auto;
  border-radius: 50%;
  width: 5em;
  margin-top: 3.8em;
}

footer {
  background: #9edccd;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 3em;
  padding-block: 4em;
  display: flex;
}

footer img {
  width: 10em;
}

footer ul {
  color: #458c7e;
  gap: 5em;
  text-decoration: none;
  list-style: none;
  display: inline-flex;
}

footer ul li:hover {
  cursor: pointer;
  transition: transform .15s ease-in, scale .15s ease-in;
  transform: translateY(-1px)scale3d(1.1, 1.1, 1);
}

.social_media {
  color: #458c7e;
  gap: 2em;
  margin-top: 1rem;
  display: flex;
}

.fa-facebook:hover, .fa-instagram:hover, .fa-twitter:hover, .fa-pinterest:hover {
  cursor: pointer;
  transition: all .2s ease-in;
  transform: translateY(-1px)scale(1.2);
}

@media (width <= 1000px) {
  .grid {
    grid-template-rows: repeat(10, 1fr);
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-areas: "sunny sunny sunny sunny"
                         "s1 s1 s2 s2"
                         "s3 s3 s4 s4"
                         "s5 s5 s6 s6"
                         "client client client client"
                         "p1 p1 p2 p2"
                         "p3 p3 p4 p4"
                         "social social social social";
    display: grid;
  }
}

@media (width <= 775px) {
  .grid {
    grid-template-rows: repeat(10, 1fr);
