                         "s1 s1 s1 s1"
                         "s3 s3 s3 s3"
                         "s4 s4 s4 s4"
                         "s5 s5 s5 s5"
                         "s6 s6 s6 s6"
                         "client client client client"
                         "p1 p1 p2 p2"
                         "p3 p3 p4 p4"
                         "social social social social";
    display: grid;
  }
}

