  header {
    height: 700px;
  }

  header nav .fa-bars {
    visibility: visible;
    cursor: pointer;
    align-items: center;
    font-size: 2em;
    position: absolute;
    right: 35px;
  }

  header nav .navy {
    display: none;
  }
}

.modal {
  color: #818498;
  background: #fff;
  width: 315px;
  height: 300px;
  margin-top: 4em;
  display: block;
  position: absolute;
  top: 45px;
  right: 40px;
}

.modal:after {
  content: "";
  background: #fff;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  right: 0;
  transform: skewY(-40deg);
}

.modal li {
  justify-content: center;
  margin-top: 2.5em;
  display: flex;
}

.modal li:nth-child(4) {
  background: #fad400;
  width: 120px;
  margin-inline: auto;
}

.modal li:nth-child(4):hover {
  color: #19536b;
  cursor: pointer;
  background: #fad400;
  transition: background .25s;
}

